import PublicClass from '@/components/organization/Course'
import TeacherIntro from '@/components/organization/TeacherIntro'
import AccountInfo from '@/components/PersonalInfo/AccountInfo'
import Message from '@/components/PersonalInfo/Message'
import CreateClass from '@/components/organization/CreateClass'
import Train from '@/components/organization/Train'
import HomeWork from '@/components/organization/HomeWork'
import Exam from '@/components/organization/Exam'

// 
import Monitor from '@/components/systemAdmin/Monitor'
import AdminMessage from '@/components/systemAdmin/Message'
import AdminCheck from '@/components/systemAdmin/Check'
import AdminManage from '@/components/systemAdmin/Manage'

// 
import Department from '@/components/platform/Department'
import PlatformCourse from '@/components/platform/Course'
import PlatformIdentity from '@/components/platform/Identity'
import PlatformCategory from '@/components/platform/Category'
import PlatformInfo from '@/components/platform/PlatformInfo'




Vue.use(Router)

const router = new Router({
    routes: [
        // 
        {
            path: '/orga',
            name: 'AppNav',
            component: AppNav,
            meta: {
                teacherAuth: true
            },
            children: [{
                    path: 'prga_intro',
                    name: '/',
                    component: OrgaIntro,
                    meta: {
                        teacherAuth: true
                    },

                },
                {
                    path: 'notice',
                    name: '/',
                    component: TeacherIntro,
                    meta: {
                        teacherAuth: true
                    },
                },
                {
                    path: 'public',
                    name: '',
                    component: PublicClass,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'private',
                    name: '',
                    component: PublicClass,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'teacher_intro',
                    name: '',
                    component: TeacherIntro,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'comments',
                    name: '',
                    component: Template,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'account',
                    name: '',
                    component: AccountInfo,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'message',
                    name: '',
                    component: Message,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'createcourse',
                    name: '',
                    component: CreateClass,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'liveclass',
                    name: '',
                    component: CreateClass,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'trainlist',
                    name: '',
                    component: Train,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'traincomments',
                    name: '',
                    component: Template,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'hwlist',
                    name: '',
                    component: HomeWork,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'hwcomments',
                    name: '',
                    component: HomeWork,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'publish',
                    name: '',
                    component: Exam,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'grades',
                    name: '',
                    component: Exam,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'evaluation',
                    name: '',
                    component: Template,
                    meta: {
                        teacherAuth: true
                    },
                },
            ]
        },
        // 
        {
            path: '/platadmin',
            name: 'PlatNav',
            component: AppNav,
            meta: {
                userAuth: true
            },
            children: [{
                path: 'account',
                name: '',
                component: Template,
                meta: {
                    userAuth: true
                },
            }, {
                path: 'notice',
                name: '',
                component: Template,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'department',
                name: '',
                component: Department,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'course',
                name: '',
                component: PlatformCourse,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'identity',
                name: '',
                component: PlatformIdentity,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'category',
                name: '',
                component: PlatformCategory,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'advertising',
                name: '',
                component: Template,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'schoolinfo',
                name: '',
                component: PlatformInfo,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'information',
                name: '',
                component: Template,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'teacher_superise',
                name: '',
                component: Template,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'student_superise',
                name: '',
                component: Template,
                meta: {
                    userAuth: true
                },

            }, ]
        },
        // 
        {
            path: '/sysadmin',
            name: 'sysNav',
            component: AppNav,
            meta: {
                adminAuth: true
            },
            children: [{
                    path: 'monitor',
                    name: '',
                    component: Monitor,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'account',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'notice',
                    name: '',
                    component: AdminMessage,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/basicinfo',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/grade',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/student',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/manage',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/join',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/train',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                },
                {
                    path: 'check',
                    name: '',
                    component: AdminCheck,
                    meta: {
                        adminAuth: true
                    },
                },
                {
                    path: 'manage',
                    name: '',
                    component: AdminManage,
                    meta: {
                        adminAuth: true
                    },
                },
                // 
                {
                    path: 'orga/basicinfo',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'orga/grade',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'orga/student',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'orga/manage',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'orga/join',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'orga/train',
                    name: '',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }
            ]
        },
        {
            path: '/403',
            name: '403',
            component: Template
        }, {
            path: '*',
            name: '404',
            component: Template
        }
    ]
})

router.beforeEach((to, from, next) => {
    sessionStorage.user_role = 10

    if (to.meta.adminAuth) {
        if (sessionStorage.user_role == 30) {
            next()
        } else {
            next({
                path: '/403'
            })
        }
    } else {
        next()
    }

    if (to.meta.userAuth) {
        if (sessionStorage.user_role == 20) {
            next()
        } else {
            next({
                path: '/403'
            })
        }
    } else {
        next()
    }

    if (to.meta.teacherAuth) {
        if (sessionStorage.user_role == 10) {
            next()
        } else {
            next({
                path: '/403'
            })
        }
    } else {
        next()
    }

    if (to.fullPath == '/' ||
        to.fullPath == '/orga' ||
        to.fullPath == '/orga/' ||
        to.fullPath == '/platadmin' ||
        to.fullPath == '/platadmin/' ||
        to.fullPath == '/sysadmin' ||
        to.fullPath == '/sysadmin/') {

        if (sessionStorage.user_role == 10) {
            next({
                path: '/orga/prga_intro'
            })
        } else if (sessionStorage.user_role == 20) {
            next({
                path: '/platadmin/account'
            })
        } else if (sessionStorage.user_role == 30) {
            next({
                path: '/sysadmin/account'
            })
        } else {
            next({
                path: '/403'
            })
        }
    }


})

export default router;


