<style lang="css" scoped>
	.el-header,
	.el-footer {
		/* background-color: #B3C0D1; */
		color: #333;
		text-align: center;
		line-height: 60px;
	}
	.el-main {
		/* background-color: #E9EEF3; */
		color: #333;
		/* text-align: center; */
	}
</style>

<template>
	<el-container>
		<el-header></el-header>
		<el-main>
			<el-table :data="tableData" style="width: 100%">
				<el-table-column prop="img" label="" width="180">
				</el-table-column>
				<el-table-column prop="name" label="" width="180">
				</el-table-column>
				<el-table-column prop="signature" label="">
				</el-table-column>
				<el-table-column prop="option" label="">
				</el-table-column>
			</el-table>
		</el-main>
	</el-container>
</template>

<script>
	export default {
		name: "",
		data() {
			return {
				tableData: [{
					img: '',
					name: '',
					signature: ',',
					option: ''
				}]
			};
		}
	}
</script>



