						<i class="el-icon-upload"></i>
						<div class="el-upload__text"><em></em></div>
					</el-upload>
				</el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          name: '',
          desc2: '1898',
          desc1: '122017122017122017...',
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
    }
  }
</script>

<style lang="css" scoped>

</style>


