import { Radar } from 'vue-chartjs' //Chart

export default { //
    extends: Radar, //Line
    mounted() {

        this.renderChart({ //.,,
            labels: ['', '', '', ''],
            datasets: [{
                label: '',
                backgroundColor: 'rgba(242,113,115,.4)',
                data: [40, 30, 10, 40]
            }, {
                label: '',
                backgroundColor: 'rgba(54,162,235,.4)',
                data: [60, 70, 90, 60]
            }]
        }, { responsive: false, maintainAspectRatio: false })

    }
}


