import { Pie } from 'vue-chartjs' //Chart

export default { //
    extends: Pie, //Line
    mounted() {

        this.renderChart({ //.,,
            labels: ['', '', ''],
            datasets: [{
                label: 'Data One',
                backgroundColor: ['rgba(242,113,115,.4)', 'rgba(64,181,178)', 'rgba(45,142,229)'],
                data: [40, 5, 55]
            }]
        }, { responsive: false, maintainAspectRatio: false })

    }
}


