<template>
	<div>
		<el-form ref="form" :model="form" label-width="80px"  >
			<el-form-item label="">
				<el-input v-model="form.name"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="form.region" placeholder="">
					<el-option label="" value="shanghai"></el-option>
					<el-option label="" value="beijing"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="5">
					<el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
				</el-col>
				<el-col class="line" :span="1" style="margin-left: 35px;">-</el-col>
				<el-col :span="5">
					<el-time-picker type="fixed-time" placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
				</el-col>
			</el-form-item>
			<el-form-item label="">
				<el-switch v-model="form.delivery"></el-switch>
			</el-form-item>
			<el-form-item label="">
				<el-switch v-model="form.delivery"></el-switch>
			</el-form-item>
			<div v-show="isVideo">
				<el-form-item label="">
					<el-upload class="upload-demo" drag action="https://jsonplaceholder.typicode.com/posts/" multiple>
						<i class="el-icon-upload"></i>
						<div class="el-upload__text"><em></em></div>
					</el-upload>
				</el-form-item>
			</div>
			<div v-show="!isVideo">
				<el-form-item label="">
					<el-input v-model="input" placeholder=""></el-input>
				</el-form-item>
			</div>
			<el-form-item>
				<el-button type="primary"></el-button>
				<el-button></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				isVideo: 'true',
				input: '',
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				}
			}
		},
		created () {
			// this.changeTableData()
		},
		watch: {
			"$route": "changeTableData"
		},
		methods: {
			changeTableData() {
				if (this.$route.fullPath == '/orga/createcourse') {
					this.isVideo = 'true'									
				}else if(this.$route.fullPath == '/orga/liveclass'){	
					this.isVideo = !this.isVideo
				}
			}
		}
	}
</script>

<style lang="css" scoped>

</style>


